<template>
    <div>
        <div class="box box-clr">
            <h1 class="title is-5">Create a vacancy</h1>
            <form @submit.prevent="handleSubmit">
                <input required v-model="form.title" type="text" class="input" placeholder="Job title">
                <input required v-model="form.company"  type="text" class="input" placeholder="Company name">
                <input required v-model="form.city"        type="text" class="input" placeholder="City name">
                <textarea required v-model="form.description" type="text" class="input" placeholder="Job Description"></textarea>
                <button class="button is-primary">Create</button>
            </form>
        </div>
    </div>
</template>



<script>
    export default {
        data(){       // State management
            return {
                form:{
                    title:'',
                    company:'',
                    city:'',
                    description:'',
                }
            }
        },
        methods:{
            handleSubmit(){
                this.$emit('something',{...this.form,id:new Date().getTime.toString()});
                for(let key in this.form){
                    this.form[key]=" "
                }
            }
        }
    }
</script>



<style scoped>

input,textarea{
    margin-bottom: 20px;
}

</style>
