<template>
 <div>
<section class="hero">
  <div class="hero-body">
    <p class="title">
      Find your dream job today
    </p>
    <p class="subtitle">
      Apply for any vacancies below 
    </p>
  </div>
</section>

<NewVacancy v-if="isAdmin" @something="handleSubmit"/>

<!-- For Looping we use v-for, while in react we use map() -->
<!-- :key="vacancy.id" -> here dynamic data is paasing i.e id , thts why we r using colon -->
<Vacancy @delete="handleDelete"
  v-for="element in vacancies"
  :vacancy="element"
  :key="element.id"
  :isAdmin="isAdmin"
  />   


 </div>
    
</template>

<script>
import {vacancies} from '../../data';
import Vacancy from '../components/Vacancy.vue';
import NewVacancy from '../components/NewVacancy.vue';


    export default {
        components:{
            NewVacancy,
            Vacancy,
        },
        data(){
          return {
            vacancies,
            isAdmin:true
            }
        },
        methods:{
          handleSubmit(formData){
            this.vacancies.unshift(formData);
            // console.log(formData);
          },
          handleDelete(vacancyId){
            this.vacancies=this.vacancies.filter(elem=>elem.id !== vacancyId);
          }
        }
    }
</script>

