<template>
    <div>
        <h1>Hey there</h1>
        <div class="box" v-for="post in posts" :key="post.id">
            <h1>
                {{post.title}}
            </h1>
        </div>
    </div>
</template>



<script>
    export default {
        data(){
            return{
                posts:[]
            }
        },
        created(){   // like useEffect() of react
            fetch('https://jsonplaceholder.typicode.com/posts')
            .then(res=>res.json())
            .then(data=>{
                this.posts=data;
            });
        }
    }
</script>



